<template>
    <el-table :data="datatable" style="width: 95%;margin-bottom: 10px;margin-left: 5%;" ref="expandTable" border>
        <el-table-column type="expand">
            <template slot-scope="scope" v-if="datatable[scope.$index].hasChildren">
                <Instancetable :classData="classData" :methodData="datatable[scope.$index]" :datatable="datatable[scope.$index].children"></Instancetable>
            </template>
        </el-table-column>
        <el-table-column prop="methodName" label="方法名" width="360">
            <template slot-scope="scope">
                <el-input placeholder="请输入内容" v-model="scope.row.methodName" clearable>
                </el-input>
            </template>
        </el-table-column>
        <el-table-column prop="requestPath" label="后置路径">
            <template slot-scope="scope">
                <el-input placeholder="请输入内容" v-model="scope.row.requestPath">
                    <template slot="prepend"></template>
                </el-input>
            </template>
        </el-table-column>
        <el-table-column prop="Operation" label="操作" width="240">
            <template slot-scope="scope">
                <el-button size="mini" type="primary" @click="handleAddInstance(scope.$index, scope.row)">新增用例
                </el-button>
                <el-button size="mini" type="danger" @click="deleteMethod(scope.$index, scope.row)">删除</el-button>
            </template>
        </el-table-column>

    </el-table>
</template>
<script>
import Instancetable from './instancetable.vue'
export default {
    name: "customtable",
    data() {
        return {
            keyword: ""
        };
    },
    components:{
        Instancetable
    },
    props: {
        datatable: Array,
        classData:Object
    },
    methods: {
        deleteMethod(index, row) {
            this.datatable.splice(index, 1);
        },
        handleAddInstance(index, row) {
            // 添加入参和出餐
            let child = {
                className: this.datatable[index].className,
                methodName: this.datatable[index].methodName,
                requestParam: "",
                responseParam: "",
                hasChildren: false,
                parentPath: this.datatable[index].parentPath,
                children: []
            };
            this.datatable[index].children.push(child);
            if (!this.datatable[index].hasChildren) {
                this.datatable[index].hasChildren = true;
            }
            this.$refs.expandTable.toggleRowExpansion(row, true);
        }
    },
    components: { Instancetable }
}
</script>
<style  scoped>
.mybutton {
    margin-bottom: 10px;

}
</style>