<template>
    <el-table :data="datatable" style="width: 95%;margin-left:5%" border>
        <el-table-column label="入参">
            <template slot-scope="scope">
                <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 10 }" placeholder="请输入内容"
                    v-model="scope.row.requestParam">
                </el-input>
            </template>
        </el-table-column>
        <el-table-column label="出参">
            <template slot-scope="scope">
                <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 10 }" placeholder="请输入内容"
                    v-model="scope.row.responseParam">
                </el-input>
            </template>
        </el-table-column>
        <el-table-column label="操作" width="180">
            <template slot-scope="scope">
                <!-- <el-button size="mini" type="primary" @click="handleRequestParam(scope.$index, scope.row)">
                    生成入参
                </el-button> -->
                <el-button size="mini" type="primary" @click="handleRequest(scope.$index, scope.row)" class="mybutton">
                    请求
                </el-button>
                <el-button size="mini" type="ok" @click="handleSave(scope.$index, scope.row)">保存
                </el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除
                </el-button>
            </template>
        </el-table-column>
    </el-table>
</template>
<script>
export default {
    name: "customtable",
    data() {
        return {
            keyword: ''
        }
    },
    props: {
        datatable: Array,
        methodData: Object,
        classData: Object
    },
    methods: {
        underline2Hump(name) {
            return name.replace(/\_(\w)/g, function (all, letter) {
                return letter.toUpperCase();
            });
        },
        hump2Underline(name) {
            return name.replace(/([A-Z])/g, '_$1').toLowerCase();
        },
        jsonToUnderline(obj) {
            var that = this
            if (obj instanceof Array) {
                obj.forEach(function (v, i) {
                    that.jsonToUnderline(v)
                })
            } else if (obj instanceof Object) {
                Object.keys(obj).forEach(function (key) {
                    var newKey = that.hump2Underline(key)
                    if (newKey !== key) {
                        obj[newKey] = obj[key]
                        delete obj[key]
                    }
                    that.jsonToUnderline(obj[newKey])
                })
            }
            return obj
        },
        jsonToHump(obj) {
            var that = this
            if (obj instanceof Array) {
                obj.forEach(function (v, i) {
                    that.jsonToHump(v)
                })
            } else if (obj instanceof Object) {
                Object.keys(obj).forEach(function (key) {
                    var newKey = that.underline2Hump(key)
                    if (newKey !== key) {
                        obj[newKey] = obj[key]
                        delete obj[key]
                    }
                    that.jsonToHump(obj[newKey])
                })
            }
            return obj
        },
        handleSave(index, row) {
            let params = {
                params: {
                    "requestParam": row.requestParam,
                    "responseParam": row.responseParam,
                    "className": row.className,
                    "methodName": row.methodName
                }
            }
            // this.$axios.post(row.parentPath,row.requestParam).then(res=>{console.log(res.data);},err=>{console.log(err);})
            this.$axios.get("http://localhost:10102/junit/genfile", params)
                .then(res => { console.log(res.data); }, err => { console.log(err); })

        },
        // handleRequestParam(index, row) {
        //     let params = {
        //         params: {
        //             "className": row.className,
        //             "methodName": row.methodName
        //         }
        //     }
        //     // this.$axios.post(row.parentPath,row.requestParam).then(res=>{console.log(res.data);},err=>{console.log(err);})
        //     this.$axios.get("http://localhost:10102/junit/getrequestparam", params)
        //         .then(res => {
        //             row.requestParam = JSON.stringify(res.data, null, 2)
        //             console.log(JSON.stringify(res.data))
        //         }, err => {
        //             console.log(err);
        //         })

        // },
        handleRequest(index, row) {
            //  TODO 驼峰转下划线 {"className":"this uis","methodNode":"asDss"}
            let params = new FormData();
            if (row.requestParam != null && row.requestParam != "") {
                params.append("requeststring",JSON.stringify(this.jsonToUnderline(JSON.parse(row.requestParam))))
                params.append("url",row.parentPath)
                // params = {
                //     "requeststring": JSON.stringify(this.jsonToUnderline(JSON.parse(row.requestParam))),
                //     "url": row.parentPath
                // }
            } else {
                params.append("requeststring",null)
                params.append("url",row.parentPath)
                // params = {
                //     "requeststring": null,
                //     "url": row.parentPath
                // }
            }
            // console.log(this.jsonToUnderline(JSON.parse(row.requestParam)))
            // this.$axios.post(row.parentPath,row.requestParam).then(res=>{console.log(res.data);},err=>{console.log(err);})
            this.$axios.post("http://localhost:10102/junit/httprequest", params)
                .then(res => {
                    console.log(JSON.stringify(res.data))
                    if (res.data.classObject != null) {
                        this.methodData.methodName = res.data.classObject.methodName;
                        this.classData.className = res.data.classObject.className;
                        console.log(JSON.stringify(res.data.classObject.className))
                        console.log(JSON.stringify(res.data.classObject.methodName))
                    }
                    console.log(row.requestParam)
                    if (row.requestParam == null || row.requestParam == "") {
                        row.requestParam = JSON.stringify(this.jsonToUnderline(JSON.parse(res.data.requestParamJson)), null, 2)
                        // row.requestParam = JSON.stringify(JSON.parse(res.data.requestParamJson), null, 2)
                    }
                    // row.responseParam = JSON.stringify(res.data.responseString.data[0], null, 2)
                    row.responseParam = JSON.stringify(this.jsonToUnderline(JSON.parse(res.data.responseString)), null, 2)
                }, err => {
                    console.log(err);
                })
        },
        handleDelete(index, row) {
            this.datatable.splice(index, 1)
        }
    }
}
</script>
<style  scoped>
.mybutton {
    margin-bottom: 10px;

}
</style>