<template>
    <div>
        <el-table :data="tableData" style="width: 100%;margin-bottom: 10px;" ref="expandTable" border>
            <el-table-column type="expand" align="right">
                <template slot-scope="scope" v-if="tableData[scope.$index].hasChildren">
                    <methodtable :classData="tableData[scope.$index]" :datatable="tableData[scope.$index].children"></methodtable>
                </template>
            </el-table-column>
            <el-table-column prop="className" label="全类名" width="360">
                <template slot-scope="scope">
                    <el-input placeholder="请输入内容" v-model="tableData[scope.$index].className" clearable>
                    </el-input>
                </template>
            </el-table-column>
            <el-table-column prop="requestPath" label="请求路径">
                <template slot-scope="scope">
                    <el-input placeholder="请输入内容" v-model="tableData[scope.$index].requestPath">
                        <template slot="prepend">前置路径</template>
                    </el-input>
                </template>
            </el-table-column>
            <el-table-column prop="Operation" label="操作" width="240">
                <template slot="header" slot-scope="scope">
                    <el-button size="mini" type="danger" @click="handleAddClass(scope.$index, scope.row)">新增子类
                    </el-button>
                </template>
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleAddMethod(scope.$index, scope.row)">新增方法
                    </el-button>
                    <el-button size="mini" type="danger" @click="deleteClass(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>

        </el-table>

    </div>
</template>
<script>
import methodtable from './methodtable.vue'
export default {
    data() {
        return {
            tableData: []
        }
    },
    components: {
        methodtable
    },
    watch: {
        tableData: {
            handler(val) {
                for (var i = 0; i < val.length; i++) {
                    if (this.tableData[i].children.length > 0) {
                        for (var j = 0; j < this.tableData[i].children.length; j++) {
                            this.tableData[i].children[j].parentPath = this.tableData[i].requestPath + this.tableData[i].children[j].requestPath
                            this.tableData[i].children[j].className = this.tableData[i].className
                            if (this.tableData[i].children[j].children.length > 0) {
                                for (var n = 0; n < this.tableData[i].children[j].children.length; n++) {
                                    this.tableData[i].children[j].children[n].parentPath = this.tableData[i].children[j].parentPath
                                    this.tableData[i].children[j].children[n].className = this.tableData[i].children[j].className
                                    this.tableData[i].children[j].children[n].methodName = this.tableData[i].children[j].methodName
                                }
                            }
                        }
                    }
                }
            },
            deep: true
        }
    },
    mounted() {
        window.myVue = this
    },
    methods: {
        deleteClass(index, row) {
            this.tableData.splice(index, 1);
        },
        handleAddClass(index, row) {
            let child =
            {
                className: "",
                requestPath: "http://localhost:8081/junit/getTestString",
                hasChildren: false,
                children: []
            }
            this.tableData.push(child)
        },
        handleAddMethod(index, row) {
            let child = {
                className: this.tableData[index].className,
                methodName:'',
                requestPath: '',
                hasChildren: false,
                parentPath: this.tableData[index].requestPath,
                children: []
            }
            this.tableData[index].children.push(child)
            if (!this.tableData[index].hasChildren) {
                this.tableData[index].hasChildren = true;
            }
            this.$refs.expandTable.toggleRowExpansion(row, true)
            // console.log(index, row);
        },
        handleDelete(index, row) {
            console.log(index, row);
        }
    },
}
</script>